$width-round: 500px;
$height-round: 500px;
$width-center: $width-round * 0.08;
$height-center: $height-round * 0.08;
$height-line-seconds: $height-round / 2;
$width-line-seconds: 5px;
$height-line-minutes: $height-round / 2;
$width-line-minutes: 10px;
$height-line-houres: $height-round / 4;
$width-line-houres: 20px;

// quantity seconds in minute
$seconds: 5s;

// quantity minutes in hour
$minutes: $seconds * 60;
$hours: $minutes * 12;
