* {
  box-sizing: border-box;
}

.watch {
  background-color: gray;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.watch__wrapper {
  background-color: green;
  border: 4px dashed #000;
  border-radius: 50%;
  width: 500px;
  height: 500px;
  position: relative;
  overflow: hidden;
}

.watch__center {
  z-index: 3;
  background-color: #ff0;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.watch__second-line {
  transform-origin: 100% 100%;
  z-index: 2;
  background-color: #fff;
  width: 5px;
  height: 250px;
  animation: 5s linear infinite watch-minutes;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%)rotate(0);
}

.watch__minute-line {
  transform-origin: 100% 100%;
  z-index: 2;
  background-color: gray;
  width: 10px;
  height: 250px;
  animation: 300s linear infinite watch-minutes;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%)rotate(0);
}

.watch__hour-line {
  transform-origin: 100% 100%;
  z-index: 1;
  background-color: #00f;
  width: 20px;
  height: 125px;
  animation: 3600s linear infinite watch-houres;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translateX(-50%)rotate(0);
}

.watch__number {
  color: red;
  padding: 10px;
  font-size: 48px;
  line-height: 1;
  position: absolute;
}

.watch__number--12 {
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.watch__number--3 {
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.watch__number--6 {
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.watch__number--9 {
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

@keyframes watch-minutes {
  0% {
    transform: translateX(-50%)rotate(0);
  }

  25% {
    transform: translateX(-50%)rotate(90deg);
  }

  50% {
    transform: translateX(-100%)rotate(180deg);
  }

  75% {
    transform: translateX(-100%)rotate(270deg);
  }

  100% {
    transform: translateX(-50%)rotate(360deg);
  }
}

@keyframes watch-houres {
  0% {
    transform: translateX(-50%)rotate(0);
  }

  25% {
    transform: translate(-50%, 10px)rotate(90deg);
  }

  50% {
    transform: translate(-150%, 10px)rotate(180deg);
  }

  75% {
    transform: translate(-100%, -10px)rotate(270deg);
  }

  100% {
    transform: translateX(-50%)rotate(360deg);
  }
}

